<template>
  <div class="app-container" id="vehicleArchives">
    <div class="filter-container">
      <el-input v-model="listQuery.licenseNo" placeholder="//4G" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="handleFilter"></el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate"  icon="el-icon-circle-plus-outline"></el-button>
      <!-- <el-button
        class="filter-item"
        type="primary"
        @click="handleCreateAll"
        v-if="power==1"
      ></el-button> -->
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading"></el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru"></el-button>


    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row.licenseNo}}</span>
            <span v-else>{{ row.licenseNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="120px" align="center" v-if="power==1">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row.serialNo}}</span>
            <span v-else>{{ row.serialNo }}</span>
          </template>
        </el-table-column>
           <!-- <el-table-column label="" width="120px" align="center" v-if="power==1">
          <template slot-scope="scope">
            <span>{{ scope.row.scanDevice }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="" width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.driverName }}</span>
          </template>
        </el-table-column> -->
      
        <!-- <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.teamName }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="" width="120px" align="center" v-if="power==1">
          <template slot-scope="scope">
            <span>{{ scope.row.bindRfid }}</span>
          </template>
        </el-table-column> -->
     
        <!-- <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ formatRunStatus(scope.row.runType)}}</span>
          </template>
        </el-table-column> -->
          <!-- <el-table-column label="" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.routeName}}</span>
          </template>
        </el-table-column>
      
        <el-table-column label="" width="180px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.brand }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.style }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDateTime(scope.row.broughtTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDateTime(scope.row.registerTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.wheelNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.oilType }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.engineType }}</span>
          </template>
        </el-table-column> -->
      

        <el-table-column label="" min-width="180px" align="center">
          <template slot-scope="scope">
            <span>{{ formatStatus(scope.row.status)}}</span>
          </template>
        </el-table-column>

    
      
        <el-table-column label="" min-width="80px" align="center" fixed="right">
          <template slot-scope="{row}">
            <!-- <el-popover placement="left" title="" width="200" trigger="click" @show="popShow(row,4)"> -->
            <span v-if="row.photoId" @click="popShow(row)" style="cursor:pointer"></span>
            <span v-else style="color: red;"></span>
            <!-- <img :src="taskSrc" class="taskSrc" alt="">
            </el-popover> -->
          </template>

        </el-table-column>
        <el-table-column label="" align="center" min-width="500" fixed="right" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <!-- <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo" v-if="power==1"></el-button> -->
            <!-- <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="row.teamId==-1&&row.driverId==-1" icon="iconfont iconsuo"></el-button> -->
            <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="row.scanDeviceId==null&&row.bindRfidId==null" icon="iconfont iconjiesuo5" v-if="power==1"></el-button>
            <el-button type="primary" size="mini" @click="handleUpdate(row)" :disabled="row.status==4" icon="el-icon-edit-outline"></el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" :disabled="row.status==4" icon="iconfont iconbaofei"></el-button>
            <el-button type="primary" size="mini" @click="handleUpload(row)" icon="iconfont iconshangchuan" ></el-button>


          </template>
        </el-table-column>  
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center :class="{'editClass':true}">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <!-- <el-form-item label="" prop="licenseNo" v-if="dialogStatus=='create'">
          <el-input v-model="temp.licenseNo" />
        </el-form-item> -->
          <el-form-item label="" prop="licenseNo">
          <el-input v-model="temp.licenseNo" :readonly="power!==1&&dialogStatus=='update'"/>
        </el-form-item>

        <!-- <el-form-item label="" prop="serialNo">
          <el-input v-model="temp.serialNo" />
        </el-form-item>
        <el-form-item label="" prop="companyName" v-if="dialogStatus=='create'">
          <el-input v-model="temp.companyName" />
        </el-form-item>
        <el-form-item label="" prop="channelNo" v-if="dialogStatus=='create'">
          <el-select v-model="temp.channelNo" class="filter-item" placeholder="Please select">
            <el-option
              v-for="item in channelNoOptions"
              :key="item.channelNo"
              :label="item.channelNo"
              :value="item.channelNo"
            />
          </el-select>
        </el-form-item> -->
        <el-form-item label="" prop="teamId" v-if="dialogStatus=='update'&&power==1">
          <el-select v-model.number="temp.teamId" class="filter-item" placeholder="Please select">
            <el-option
              v-for="item in largeTeamOptions"
              :key="item.id"
              :label="item.teamName"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="" prop="groupId" v-if="dialogStatus=='update'">
          <el-select v-model.number="temp.groupId" class="filter-item" placeholder="Please select">
            <el-option
              v-for="item in groupsOptions"
              :key="item.id"
              :label="item.groupName"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="4G" prop="chinaMobileNo" v-if="dialogStatus=='update'">
          <el-input v-model="temp.chinaMobileNo" />
        </el-form-item> -->

        <el-form-item label="" prop="brand">
          <el-input v-model="temp.brand" />
        </el-form-item>

        <el-form-item label="" prop="style">
          <el-input v-model="temp.style" />
        </el-form-item>
        <el-form-item label="" prop="broughtTimeShow">
          <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="temp.broughtTimeShow" align="right" type="datetime" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="registerTimeShow">
          <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="temp.registerTimeShow" align="right" type="datetime" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="wheelNo">
          <el-input v-model="temp.wheelNo" />
        </el-form-item>
        <el-form-item label="" prop="oilType">
          <el-input v-model="temp.oilType" />
        </el-form-item>
        <el-form-item label="" prop="engineType">
          <el-input v-model="temp.engineType" />
        </el-form-item>
        <!-- <el-form-item label="" prop="carIMSI">
          <el-input v-model="temp.carIMSI " />
        </el-form-item> -->

        <el-form-item label="" prop="status">
          <el-select v-model="temp.status" class="filter-item" placeholder="Please select">
            <el-option v-for="item in carUsageOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="runType">
          <el-select v-model="temp.runType" class="filter-item" placeholder="Please select">
            <el-option v-for="item in runOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()"></el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" @close="scrollTop" title="" :visible.sync="dialogVisible" center>
      <el-form ref="addAllForm" :rules="rules1" :model="addAlltemp" label-position="left" class="addAllForm">
        <el-form-item label="" prop="head">
          <el-input v-model="addAlltemp.head" placeholder="(,CX051234CX05)" class="carNo" />
        </el-form-item>

        <el-form-item label="" prop="begin">
          <el-input v-model="addAlltemp.begin" />
        </el-form-item>
        <el-form-item label="" prop="end">
          <el-input v-model="addAlltemp.end" />
        </el-form-item>
        <el-form-item label="" prop="channelNo">
          <el-select v-model="addAlltemp.channelNo" class="filter-item" placeholder="Please select">
            <el-option v-for="item in channelNoOptions" :key="item.channelNo" :label="item.channelNo" :value="item.channelNo" />
          </el-select>
        </el-form-item>
        <div style="color:#FF3F3F;text-align:center;margin-top:8px;"></div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createAllCar()"></el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" :model="bindTemp" label-position="left" class="formStyle">

        <el-form-item :label="`${bindMap[bindDialogStatus]}`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="Please select">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled" />
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="" prop="driverId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.driverId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in driverOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="teamId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.teamId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item> -->
        <el-form-item label="" prop="rfidId" v-if="bindTemp.type==3&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.rfidId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in rfidOptions" :key="item.id" :label="item.rfid" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="scanDeviceId" v-if="bindTemp.type==4&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.scanDeviceId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in scanOptions" :key="item.id" :label="item.scanDevice" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()"></el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog1" title="" :visible.sync="DialogUploadVisible" center>
      <el-upload class="upload-demo" ref="my-upload" drag action="/gate/file/xxx/upload" accept=".jpg,.png" :before-upload="beforeUploadFile"  :headers="{Authorization:'Bearer '+ token}"  :on-success="uploadSuccess" :multiple="false" :limit="1" >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png,10M</div>
        <div class="el-upload__tip red" slot="tip"> 295*413 </div>

      </el-upload>
      <div slot="footer" class="dialog-footer">
        <!-- <el-button type="primary" ></el-button> -->

      </div>
    </el-dialog>
    <el-dialog id="myDialog2" title="" width="30%" :visible.sync="imgVisible" center>
      <img :src="taskSrc" class="taskSrc" alt="">
    </el-dialog>
    
<my-upload :visible.sync="uploadVisible"  :uploadData="{type:2}" @dataUpdated="reqSuccess"></my-upload>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination

//
import {
  carlist,
  carAdd,
  cardelete,
  carupdate,
  carSearch,
  addBatch,
  setPause,
  discardCar,
  bindDriver,
  unbindDriver,
  bindTeam,
  unbindTeam,
  carBindScan,
  carBindRfid,
  carUnbindScan,
  carUnbindRfid,
  carsBindPhoto
} from "../../../api/managementArchives/vehicleArchives";
import { formatDate } from "../../../../src/common"; //
//
import {
  teamsSelect,
  personsSelect,
  exportExcelReq,
  importExcelReq,
  uploadReq
} from "../../../api/managementArchives/common";
import { queryR } from "../../../api/inOut/rfid";
import { queryScan } from "../../../api/inOut/scanDevice";
import {
  imgDownload, savePhoto, photoInfosQuery
} from "../../../api/schedule/schedule";

const carUsageOptions = [
  {
    key: 1,
    display_name: ""
  },
  {
    key: 2,
    display_name: ""
  },
  {
    key: 3,
    display_name: ""
  },
  {
    key: 4,
    display_name: ""
  }
];

//
const reasonableLen = (rule, value, callback) => {
  const reasonableLength = /[0-9]{4}/;
  if (!reasonableLength.test(value)) {
    callback(new Error("4"));
  } else {
    callback();
  }
};
//
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error(""));
  } else {
    callback();
  }
};
let myLoading = undefined; // 
export default {
  name: "vehicleArchives",
  components: {
    Pagination,
    myUpload
  },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 
      token: JSON.parse(sessionStorage.getItem("userInfo"))['Bearer '],

      tableKey: 0,
      list: null, //
      total: 0, //
      listLoading: true,
      indexMethod: 1, // 
      listQuery: {
        page: 1,
        limit: 10,
        licenseNo: undefined
      },
      carUsageOptions, //
      runOptions: [
        {
          key: -1,
          display_name: ""
        },
        {
          key: 0,
          display_name: ""
        },
        {
          key: 1,
          display_name: ""
        }
      ], //
      groupsOptions: [], //

      largeTeamOptions: [], //
      channelNoOptions: [], //
      //
      temp: {
        licenseNo: undefined,
        // carName: undefined,
        // serialNo: undefined,
        // teamId: undefined,
        // teamName: undefined,
        // chinaMobileNo: null,
        brand: "",
        style: "",
        broughtTime: null,
        registerTime: null,
        wheelNo: "",
        oilType: "",
        engineType: "",
        // carIMSI: undefined,
        status: 1,
        runType: -1,
        // routeId: null,
        // routeName: null,
        // channelNo: null
      },
      dialogFormVisible: false, //
      dialogStatus: "", //
      textMap: {
        update: "",
        create: ""
      },
      //
      rules: {
        // carName: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
        // serialNo: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
        licenseNo: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
        // ,
        // companyName: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
        // channelNo: [
        //   {
        //     message: "",
        //     trigger: "change",
        //     required: true
        //   }
        // ]
      },
      //
      rules1: {
        head: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        begin: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { validator: reasonableLen, trigger: "blur" }
        ],
        end: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { validator: reasonableLen, trigger: "blur" }
        ],
        channelNo: [
          {
            message: "",
            trigger: "change",
            required: true
          }
        ]
      },

      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      dialogVisible: false, //
      addAlltemp: {
        head: undefined,
        begin: undefined,
        end: undefined,
        channelNo: null
      },
      bindTypeOptions: [
        // { id: -1, name: "" },
        // { id: 1, name: "" },
        // { id: 2, name: "" },
        { id: 3, name: "" },
        { id: 4, name: "" },

      ], //
      bindTemp: {

        type: 4
      },

      BindDialogFormVisible: false,
      bindDialogStatus: "",//
      bindMap: {
        bind: "",
        unbind: ""
      },
      bindRules: {
        type: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        driverId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        teamId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        rfidId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ],
        scanDeviceId: [
          { required: true, message: "", trigger: "change", validator: unSelect }
        ]
      },
      rfidOptions: [],
      scanOptions: [],
      fileList: [],
      DialogUploadVisible: false,
      carId: null,
      imgVisible: false,
      taskSrc: '',
      uploadVisible:false,
      exportLoading: false,

    };
  },
  created() {
    this.getList(); //
    this.getSelect(); //
    this.getChannelNoOptions(); //
  },
  mounted() { },
  methods: {
   deleteConfirmOpen(row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.discard(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
     // , , false  Promise  reject
    beforeUploadFile(file) {
    
      let extension = file.name.substring(file.name.lastIndexOf('.') + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== 'jpg' && extension !== 'png'&&extension !== 'JPG' && extension !== 'PNG') {
        this.$message.warning('.jpg.png');
        return false;
      }
      if (size > 10) {
        this.$message.warning('10M');
        return false;

      }
    },
    reqSuccess: function(val){  //val
      if(val==true){
        this.getList();
      }
    },
    popShow(row) {
      
      this.listLoading=true;
      this.taskSrc = "";
      let that = this;
        if(row.photoId==null){
          this.imgVisible = true;
        this.listLoading=false;  
      return;
      }
      photoInfosQuery({ id: row.photoId,  type: [2 ]})
        .then(res => {
          if (res.status == 0) {
             if(res.data&&res.data.length==0){
               that.imgVisible = true;
               return;
             }
            imgDownload({ path: res.data[0].path, fileName: res.data[0].fileName })
              .then(res1 => {
                that.imgVisible = true;
                 that.listLoading=false;
                  that.taskSrc = window.URL.createObjectURL(res1)
              })
              .finally(() => {
                that.listLoading=false;
              });

          } else {
            this.$message.error(res.msg);
          }
        }).catch(()=>{
         that.listLoading=false;
        })
        .finally(() => {
        });

    },
    handleUpload(row) {
      this.$nextTick(() => {
        this.$refs['my-upload'].clearFiles();

      })
      this.DialogUploadVisible = true;
      this.carId = row.id
    },
     uploadSuccess(res) {
      if (res.data == null) {
        return;
      }
      let p1 = new Promise((resolve, reject) => {
        savePhoto({
          fileName: res.data.filename,
          path: res.data.path,
          type: 2
        })
          .then(res => {
            resolve(res);

          }).catch(err => {
            reject(err);

          })


      })

      p1.then((res) => {

        carsBindPhoto({
          carId: this.carId,
          photoId: res.data
        })
          .then(res1 => {
            if (res1.status == 0) {
              this.DialogUploadVisible = false;
              this.$message.success(res1.msg);
              this.getList();

            } else {
              this.$message.error(res1.msg);
            }
          }).catch(() => {
            this.$message.error(res1.msg);
          })
          .finally(() => {
            _t.listLoading = false;
          });


      }, (err) => {
        _t.listLoading = false;
        this.$message.error(err.msg);
      })



    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
     getRfid(row) {
       let that = this;
      this.rfidOptions = [];
       queryR({ rfidType:[9],status:[0]  }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.rfidOptions = res.data;
          that.rfidOptions.unshift({ id: null, rfid: "" });
        } else {
          if (res.data == null) {
            this.rfidOptions = [];
            this.rfidOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      
      
    },
    handleBind(row) {
      this.getRfid(row);

      this.bindTypeOptions = [
        // { id: -1, name: "" },
        // { id: 1, name: "" },
        // { id: 2, name: "" },
        // { id: 3, name: "" },
        { id: 4, name: "" },

      ];
      this.bindDialogStatus = "bind";
      // this.bindTemp = {
      //   type: 1,
      //   carId: row.id,
      //   driverId: row.driverId,
      //   teamId: row.teamId,
      //   rfidId: row.bindRfidId,
      //   scanDeviceId: row.scanDeviceId,
      // }
       this.bindTemp = {
        type: 4,
        carId: row.id,
        driverId: row.driverId,
        teamId: row.teamId,
        // rfidId: row.bindRfidId,
        rfidId: null,

        scanDeviceId: row.scanDeviceId,
      }
      this.BindDialogFormVisible = true;
    },
    handleUnbind(row) {
      // if (row.driverId === -1) { this.bindTypeOptions[1].disabled = true }
      // if (row.teamId === -1) { this.bindTypeOptions[2].disabled = true }
      // if (row.rfidId === null) { this.bindTypeOptions[3].disabled = true }
      // if (row.scanDeviceId === null) { this.bindTypeOptions[4].disabled = true }
      //  if (row.bindRfidId === null) { this.bindTypeOptions[0].disabled = true }
      if (row.scanDeviceId === null) { this.bindTypeOptions[0].disabled = true }
      this.bindDialogStatus = "unbind";
      // this.bindTemp = {
      //   carId: row.id,
      //   type: 1
      // }
       this.bindTemp = {
        carId: row.id,
        type: 4
      }
      this.BindDialogFormVisible = true;
    },
    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData, request;
          if (this.bindTemp.type == 1) {
            subData = {
              carId: this.bindTemp.carId,
              driverId: this.bindTemp.driverId,
            }
            request = bindDriver;

          } else if (this.bindTemp.type == 2) {
            subData = {
              carId: this.bindTemp.carId,
              teamId: this.bindTemp.teamId,
            }
            request = bindTeam;
          } else if (this.bindTemp.type == 3) {
            subData = {
              carId: this.bindTemp.carId,
              rfidId: this.bindTemp.rfidId,
            }
            request = carBindRfid;
          } else if (this.bindTemp.type == 4) {
            subData = {
              carId: this.bindTemp.carId,
              scanDeviceId: this.bindTemp.scanDeviceId,
            }
            request = carBindScan;
          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect();

                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },
    unbindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData, request;
          subData = {
            carId: this.bindTemp.carId
          }
          if (this.bindTemp.type == 1) {

            request = unbindDriver;

          } else if (this.bindTemp.type == 2) {

            request = unbindTeam;


          } else if (this.bindTemp.type == 3) {

            request = carUnbindRfid;


          } else if (this.bindTemp.type == 4) {

            request = carUnbindScan;


          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      })
    },
    //
    getList(type) {
      let _t = this;
      _t.listLoading = true;
      carlist({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status:[1,2,3]
      })
        .then(res => {
          if (res.status == 0) {
            console.log(res.data);
            this.listLoading = false;
            this.list = res.data;

            this.total = res.count;
            //
            this.dealData();
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
            if(type==1){

            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //id-1
    //id-1,null,
    dealData() {
      this.list.forEach((item, index) => {
        this.list[index].broughtTimeShow = formatDate(item.broughtTime, 1);
        this.list[index].registerTimeShow = formatDate(item.registerTime, 1);
      });
    },
    //
    getChannelNoOptions() {
      this.channelNoOptions = [];
      for (let i = 0; i < 17; i++) this.channelNoOptions.push({ channelNo: i });
    },
    //
    formatStatus(status) {
      if (status == 1) {
        return "";
      } else if (status == 2) {
        return "";
      } else if (status == 3) {
        return "";
      } else if (status == -1) {
        return "";
      } else if (status == 4) {
        return ""
      }
    },
    //
    formatRunStatus(status) {
      if (status == 0) {
        return "";
      } else if (status == 1) {
        return "";
      } else if (status == -1) {
        return ""
      }
    },
    //
    handleFilter() {
      if (
        this.listQuery.licenseNo == "" ||
        this.listQuery.licenseNo == undefined
      ) {
        this.listQuery.licenseNo = null;
        this.getList(1);
        return;
      }
      carSearch({
        keyword: this.listQuery.licenseNo
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;
          this.total = 0;
          this.dealData();
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    //
    resetTemp() {
      this.temp = {
        licenseNo: undefined,
        // carName: undefined,
        // serialNo: undefined,
        // teamId: undefined,
        // teamName: undefined,
        // chinaMobileNo: undefined,
        brand: "",
        style: "",
        wheelNo: "",
        oilType: "",
        engineType: "",
        // carIMSI: undefined,
        status: 1,
        runType: -1,
        // routeId: null,
        // routeName: null,
        broughtTimeShow: null,
        registerTimeShow: null,
        // channelNo: null
      };
    },
    //
    resetAddAllTemp() {
      this.addAlltemp = {
        head: undefined,
        begin: undefined,
        end: undefined,
        channelNo: null
      };
    },
    //
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //
    handleCreateAll() {
      this.resetAddAllTemp();
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs["addAllForm"].clearValidate();
      });
    },
    //
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          if (this.temp.broughtTimeShow) { this.temp.broughtTime = new Date(this.temp.broughtTimeShow).getTime(); }
          if (this.temp.registerTimeShow) { this.temp.registerTime = new Date(this.temp.registerTimeShow).getTime(); }
          let subData = { ...this.temp };
          delete subData.broughtTimeShow
          delete subData.registerTimeShow
          if (subData.status == -1) { delete subData.status }
          if (subData.runType == -1) { delete subData.runType }
          carAdd(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //
    createAllCar() {
      this.$refs["addAllForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          addBatch(this.addAlltemp)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogVisible = false;
                //imeicarAndGunObj
                this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },

    //
    handleUpdate(row) {
      this.temp = {
        brand: row.brand,
        style: row.style,
        id: row.id,
        wheelNo: row.wheelNo,
        oilType: row.oilType,
        engineType: row.engineType,
        status: row.status,
        runType: row.runType,
        broughtTimeShow: row.broughtTimeShow,
        registerTimeShow: row.registerTimeShow,
        licenseNo:row.licenseNo


      },
        this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          this.temp.broughtTime = new Date(this.temp.broughtTimeShow).getTime();
          this.temp.registerTime = new Date(this.temp.registerTimeShow).getTime();
          let subData = { ...this.temp };
          delete subData.broughtTimeShow
          delete subData.registerTimeShow
          carupdate(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //
    discard(row) {
      let _t = this;
      _t.listLoading = true;
      discardCar({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
          console.error(err);
        });
    },
    //
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      cardelete({
        id: row.id,
        licenseNo: row.licenseNo
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("");
            //imeicarAndGunObj
            this.$store.commit("realTimeMonitor/ISEDITFILES", true);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
          console.error(err);
        });
    },
    //
    formatDateTime(timestamp) {
      return formatDate(timestamp, 1);
    },
    //selectselectv-modeloptionoptionoptionid,name
    selectFilter(vId, selectList, id, name, id1, name1) {
      let obj = {};
      obj = selectList.find(item => {
        //selectList
        return item[id] === vId; //
      });
      this.temp[name1] = obj[name];
    },
    //
    getSelect() {
      var that = this;
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.unshift({ id: -1, teamName: "" });
            return;
          }
          that.largeTeamOptions = res.data;
          that.largeTeamOptions.unshift({ id: -1, teamName: "" });
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      personsSelect({ work: 1, status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.driverOptions = res.data;
          that.driverOptions.unshift({ id: -1, name: "" });
        } else {
          if (res.data == null) {
            this.driverOptions = [];
            this.driverOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
     
      queryScan({ type: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.scanOptions = res.data;
          that.scanOptions.unshift({ id: -1, scanDevice: "" });
        } else {
          if (res.data == null) {
            this.scanOptions = [];
            this.scanOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
    },
    //idnull,namenull,
    changToNull(data) {
      if (data.teamId == null) {
        data.teamName = null;
      }
      if (data.groupId == null) {
        data.groupName = null;
      }
      return data;
    },
    scrollTop() {
      // 
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 
      exportExcelReq({ excelType: 8, title: '' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      }); 
    }
  }
};
</script>

