import request from '../../request'
//
export function savesScan(data) {
    return request({
        url: '/eoms/scandevices/save',
        method: 'post',
        data

    })
}

//
export function permitBindScan(data) {
    return request({
        url: '/eoms/scandevices/permitBind',
        method: 'post',
        data

    })
}

//
export function queryScan(data) {
    return request({
        url: '/eoms/scandevices/query',
        method: 'post',
        data,
        headers:{'content-type':'application/json'},



    })
}
//
export function queryScanPage(data) {
    return request({
        url: '/eoms/scandevices/page',
        method: 'post',
        data,
        headers:{'content-type':'application/json'},



    })
}
//
export function updateScan(data) {
    return request({
        url: '/eoms/scandevices/update',
        method: 'post',
        data

    })
}